<template>
  <div id="map">
  </div>
</template>
 
<script>
export default {
  name: 'Map',
  data () {
    return {
    }
  },
  methods: {
    createMap () {
      /* eslint-disable */
      // 创建Map实例
      // var map = new BMap.Map("map")
      // 初始化地图,设置中心点坐标和地图级别
      // map.centerAndZoom(new BMap.Point(119.176895, 26.072713), 15)
      // //添加地图类型控件
      // map.addControl(new BMap.MapTypeControl({
      //   mapTypes:[BMAP_NORMAL_MAP, BMAP_HYBRID_MAP]
      // }))
      // // 设置地图显示的城市 此项是必须设置的
      // map.setCurrentCity("福州")
      // //开启鼠标滚轮缩放
      // map.enableScrollWheelZoom(true)
      // /* eslint-enable */
      var map = new BMapGL.Map('map')
      var point = new BMapGL.Point(119.16394,26.06679); // 创建点坐标
        map.centerAndZoom(point, 10);
        map.enableScrollWheelZoom(); // 启用滚轮放大缩小
        var marker1 = new BMapGL.Marker(new BMapGL.Point(119.17573,26.073617));
        map.addOverlay(marker1);
    }
  },
  mounted () {
    this.createMap()
  }
}
</script>
<style scoped >
#map{
  min-height:50vh;
  min-width:50vw;
}
</style>